import React from 'react'
import ImportExportIcon from '@mui/icons-material/ImportExport';
import NorthIcon from '@mui/icons-material/North';
import SouthIcon from '@mui/icons-material/South';

//      
const downIcon = SouthIcon // '[\\/]'
const upIcon = NorthIcon //'[/\\]'
const noneIcon = ImportExportIcon //'[--]'

export type SuperSortPropsType = {
  id?: string
  sort: string
  value: string
  onChange: (newSort: string) => void
}

export const pureChange = (sort: string, down: string, up: string) => {
  //  , sort: (click) => down (click) => up (click) => '' (click) => down ...

  return sort === down ? up : sort === up ? '' : down
}

const SuperSort: React.FC<SuperSortPropsType> = (
   {
     sort, value, onChange, id = 'hw15',
   }
) => {
  const up = '0' + value
  const down = '1' + value


  const onChangeCallback = () => {
    onChange(pureChange(sort, down, up))
  }

  const icon = sort === down
     ? downIcon
     : sort === up
        ? upIcon
        : noneIcon

  const IconComponent =
     sort === down ? SouthIcon : sort === up ? NorthIcon : ImportExportIcon;

  return (
     <span
        id={id + '-sort-' + value}
        onClick={onChangeCallback}
     >
       {/*<img*/}
       {/*   id={id + '-icon-' + sort}*/}
       {/*   src={icon}*/}
       {/*/>*/}

       <IconComponent id={id + '-icon-' + sort}/>

       {/*{icon} /!*  *!/*/}
        </span>
  )
}

export default SuperSort
